.company-info {
    width: 100%;
    background-color: rgb(34, 49, 61);
    color: white;
    display: flex;
    height: max-content;
    padding: 0px;
}

.company-info-box {
    display: flex;
    flex-direction: row;
    margin-inline: auto;
    margin-block-start: 150px;
    margin-block-end: 100px;
}

.company-description {
    vertical-align: middle;
    height: min-content;
    max-width: 600px;
    margin-right: 50px;
    margin-inline: 50px;
}

.image-container {
    height: 100%;
    width: auto;
    margin-right: 60px;
}

.company-description h2 {
    font-size: 30px;
    margin-block-end: 20px;
}

.company-description p {
    margin-block-end: 20px;
}


@media screen and (max-width: 700px) {
    .company-info-box {
        display: flex;
        flex-direction: column;
    }

    .image-container {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        margin-top: 30px;
        margin-bottom: 0px;
    }

}